import React from 'react';
import s from './Home.module.css';
import cities, { CityKey } from 'data/cities';
import _ from 'lodash';
import { useCity } from 'pages/Main';
import Button from "ui/Button";
import { history } from 'index';


const VacanciesList: React.FC = () => {
  const [stateCity] = useCity();
  const openVacancy = (vacancyId: string) => () => {
    if (vacancyId) {
      history.push(`/vacancy/${vacancyId}#form`);
    }
  };
  const renderVacancies = () => {
    if (_.isEmpty(stateCity)) {
      return null;
    }

    const currentCity = cities[(stateCity.id as CityKey)];

    if (!currentCity || _.isEmpty(currentCity.vacancies)) {
      return null;
    }

    const list = _.map(currentCity.vacancies, (v) => {

      return (
        <div key={v.id} className={s.vacancy} id="vacancies">
          <img src={v.image} className={s.vacancyImage} alt="vacancy"/>
          <div className={s.position}>
            <div className={s.vacancyName}>{v.title}</div>
            <div className={s.vacancyDescription}>
              {v.desc}
            </div>
            <div className={s.vacancyDescription}>
              <span className={s.important}>{v.position}</span>&nbsp;
              ({v.schedule})
            </div>
            <div className={s.offerPrice}>{v.salary}</div>
            <Button onClick={openVacancy(v.id)} className={s.goInterview} text=" "/>
          </div>
        </div>
      );
    });

    return (
      <div className={s.vacancies}>
        {list}
      </div>
    );
  };

  return (
    <div className={s.openVacancies}>
      <div className={s.title}> </div>
      {renderVacancies()}
    </div>
  );
};

export default VacanciesList;
