import React, {useState} from 'react';
import s from './Vacancy.module.css';
import Features from 'ui/Sections/Features';
import Steps from 'ui/Sections/Steps';
import VacancyBlock from './VacancyBlock';
import cities, {CityKey} from 'data/cities';
import _ from 'lodash';
import InputElement from 'react-input-mask';

import offering from 'images/offering.jpg';
import requirements from 'images/requirements.jpg';
import vacancyGirl from 'images/vacancyGirl.png';
import Button from 'ui/Button';

interface Props {
  id: string,
}

const Vacancy: React.FC<Props> = (props) => {

  const [errors, setError] = useState({
    firstNameError: '',
    lastNameError: '',
    mobilePhoneError: '',
    emailAddressError: '',
    birthDateError: '',
    formError: '',
  });

  const [form, setValues] = useState({
    firstName: '',
    lastName: '',
    mobilePhone: '',
    emailAddress: '',
    birthDate: '',
  });

  const updateField = (e: any) => {
    setValues({
      ...form,
      [e.target.name]: e.target.value
    });
    switch (e.target.name) {
      case 'emailAddress':
        setError({
          ...errors,
          emailAddressError: e.target.value.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i) ?
            '' : '  Email'
        });
        break;
      case 'mobilePhone':
        setError({
          ...errors,
          mobilePhoneError: e.target.value.match(/^(\s*)?(\+)?([- _():=+]?\d[- _():=+]?){10,14}(\s*)?$/) ?
            '' : '   '
        });
        break;
      case 'birthDate':
        setError({
          ...errors,
          birthDateError: e.target.value.match(/(0[1-9]|[12][0-9]|3[01])[- /.](0[1-9]|1[012])[- /.](19|20)\d\d/) ?
            '' : '   '
        });
        break;
    }
  };

  const reformDate = () => {
      const newDate = form.birthDate.split("/");
      return newDate[2]+"-"+newDate[1]+"-"+newDate[0];
  };


  const handleSubmitForm = () => {
    const formData = new FormData();
    const payload = {
      sourceId: '2003',
      vacancyId: props.id,
      FirstName: form.firstName,
      LastName: form.lastName,
      MobilePhone: form.mobilePhone,
      EmailAddress: form.emailAddress,
      BirthDate: reformDate(),
    };
    formData.append('candidateRequestPublic', JSON.stringify(payload));

    const xhr = new XMLHttpRequest();
    xhr.open('POST', `https://public.skillaz.co/api/v1/candidates/requests/${props.id}`, false);
    xhr.onload = () => {
      const data = JSON.parse(xhr.response);
      if (data.InterviewUrl) {
        window.location.href = data.InterviewUrl;
      }
    };
    try {
      xhr.send(formData);
    } catch (e) {
      setError({
        ...errors,
        formError: '  ',
      });
    }
  };

  const renderVacancyBlock = () => {
    const vacancies = _.flatMap((_.keys(cities) as CityKey[]), city => cities[city].vacancies);
    const vacancy = _.find(vacancies, v => v.id === props.id);
    if (!vacancy) {
      return null;
    }

    return (
      <VacancyBlock
        title={vacancy.title}
        desc={vacancy.desc}
        schedule={vacancy.schedule}
        salary={vacancy.salary}
        image={vacancy.banner}
        color={vacancy.color}
        position={vacancy.position}
      />
    );
  };

  const renderError = (error: string) => {
    if (!error) return null;
    return (
      <div className={s.error}>
        {error}
      </div>
    );
  };

  return (
    <div className={s.header}>
      {renderVacancyBlock()}
      <Features/>
      <div className={s.points}>
        <div className={s.title}> </div>
        <div className={s.pointsList}>
          <div className={s.sellPoint}>
            <div className={s.subTitle}></div>
            <div className={s.pointDescription}>
               -          ,
                  
            </div>
          </div>
          <div className={s.helpPoint}>
            <div className={s.subTitle}></div>
            <div className={s.pointDescription}>
                          
            </div>
          </div>
          <div className={s.suggestPoint}>
            <div className={s.subTitle}></div>
            <div className={s.pointDescription}>
                  -,    
            </div>
          </div>
        </div>
      </div>
      <Steps/>
      <div className={s.listContainer}>
        <div className={s.listBlock}>
          <img src={requirements} alt='requirements' className={s.listHeader}/>
          <div className={s.listContent}>
            <div className={s.listTitle}>
                ,  :
            </div>
            <ul className={s.listIn}>
              <li>   /  ;</li>
              <li>  Microsoft Office, Internet Explorer, Outlook;</li>
              <li>   ;</li>
              <li>         ;</li>
              <li>   ,   ;</li>
              <li>      ,    ;</li>
              <li>     .</li>
            </ul>
          </div>
        </div>
        <div className={s.listBlock}>
          <img src={offering} alt='offering' className={s.listHeader}/>
          <div className={s.listContent}>
            <div className={s.listTitle}>
               :
            </div>
            <ul className={s.listOut}>
              <li>   </li>
              <li>  </li>
              <li>    </li>
              <li>    / </li>
              <li>  </li>
              <li>,       </li>
              <li>    ,   </li>
              <li>  </li>
              <li>    </li>
            </ul>
          </div>
        </div>
      </div>
      <div className={s.form}>
        <img src={vacancyGirl} alt='vacancyGirl' className={s.vacancyGirl}/>
        <div className={s.formControl}>
          <div className={s.fromTitle}>
              !
          </div>
          <div className={s.formDesc}>
                
          </div>
          <div className={s.formBlock} id='form'>
            <input
              value={form.firstName}
              className={s.formInput}
              onChange={updateField}
              name='firstName'
              type='text'
              placeholder=''
            />
            {renderError(errors.firstNameError)}
            <input
              value={form.lastName}
              className={s.formInput}
              onChange={updateField}
              name='lastName'
              type='text'
              placeholder=''
            />
            {renderError(errors.lastNameError)}
            <input
              value={form.emailAddress}
              className={s.formInput}
              onChange={updateField}
              name='emailAddress'
              type='text'
              placeholder={'E-Main'}
            />
            {renderError(errors.emailAddressError)}
            <InputElement
              value={form.mobilePhone}
              className={s.formInput}
              onChange={updateField}
              name='mobilePhone'
              type='text'
              placeholder=' '
              mask="99999999999"
            />
            {renderError(errors.mobilePhoneError)}
            <InputElement
              value={form.birthDate}
              className={s.formInput}
              onChange={updateField}
              name='birthDate'
              placeholder=' '
              mask="99/99/9999"
            />
            {renderError(errors.birthDateError)}
            <Button
              onClick={handleSubmitForm}
              className={s.formSubmit}
              disabled={false} //
              type={'submit'}
              text=' '
            />
            {renderError(errors.formError)}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Vacancy;
